
import shave from 'shave'

const setOverflow = (elementClass, value, expand) => {
  const overflowElements = document.querySelectorAll(elementClass)

  overflowElements.forEach(element => {
    element.style.position = 'relative'
    element.style.transition = 'all ease 0.5s'
    const textStyle = window.getComputedStyle(element, null)
    const lineHeight = Math.round(Number(textStyle.getPropertyValue('line-height').replace('px', '')))
    const textHeight = Math.round(Number(textStyle.getPropertyValue('height').replace('px', '')))
    let button, clipText

    if (textHeight / lineHeight >= value) {
      element.style.height = `${lineHeight * value}px`
      element.style.overflow = 'hidden'
    }
    shave(elementClass, lineHeight * value, { character: '<a class="review-item__full-text">Развернуть текст</a>' })
    button = element.querySelector('.review-item__full-text')
    clipText = element.querySelector('.js-shave')
    if (button) {
      button.addEventListener('click', () => {
        if (expand) {
          element.style.height = `${textHeight}px`
          clipText.style.display = 'block'
        }
      })
    }
  })
}

window.addEventListener('load', () => {
  setOverflow('.review-item__text', 3, false)
  setOverflow('.seo-text__content', 5, true)
})
